import "./App.css";
import React from "react";
import { Table } from "antd";
import API_URL from "./config";

function App() {
  const [dataSource, setDataSource] = React.useState([]);
  const columns = [
    {
      title: "Token ID",
      dataIndex: "token_id",
      key: "token_id",
    },
    {
      title: "Ask Price",
      dataIndex: "ask_price",
      key: "ask_price",
    },
    {
      title: "Ask Sale Date",
      dataIndex: "ask_sale_date",
      key: "ask_sale_date",
    },
    {
      title: "Last Sale",
      dataIndex: "last_sale",
      key: "last_sale",
    },
    {
      title: "Last Sale Date",
      dataIndex: "last_sale_date",
      key: "last_sale_date",
    },
    {
      title: "Opensea Link",
      dataIndex: "opensea_link",
      key: "opensea_link",
    },
  ];

  React.useEffect(() => {
    fetch(API_URL + "/data")
      .then((response) => response.json())
      .then((result) => setDataSource(result))
      .catch((error) => console.log(error));
  }, []);

  return (
    <div style={{ padding: 10 }}>
      <Table dataSource={dataSource} columns={columns} />
    </div>
  );
}

export default App;
